import { Grid, Typography, Box } from '@material-ui/core';
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router'
import axiosApiInstance from '../../axios';
import ElementCard from '../../components/ElementCard/ElementCard';

export default function OneSongPage() {

	const { id } = useParams();
	const [song, setSong] = useState(null);

	useEffect(() => {

		axiosApiInstance.get(`/songs/${id}`)
			.then((res) => {
				setSong(res.data);
			})
			.catch(err => {
				console.log(err);
			})

	}, [id])

	return (
		<>
			{song ?
				<>
					<Box mb={2}>
						<Typography variant="h3" align="center">{song.name}</Typography>
						<Grid container spacing={4} justifyContent="center">
							<Grid item>
								<Typography>BPM: {song.bpm}</Typography>
							</Grid>
							<Grid item>
								<Typography>KEY: {song.key}</Typography>
							</Grid>
						</Grid>
					</Box>
					{song.elements.map(el => (
						<ElementCard
							key={el._id}
							strings={el.strings}
						/>
					))}
				</>
				: 'song loading or 404'
			}
		</>
	)
}
