import React from 'react'
import { Box } from '@material-ui/system'
import { Card, Typography, CardContent } from '@material-ui/core'

export default function ElementCard({
	strings,
}) {
	return (
		<Box mb={1}>
			<Card>
				<CardContent>
					{strings.map(str => (
						getTypography(str.string, str.type)
					))}
				</CardContent>
			</Card>
		</Box>
	)
}

function getTypography(str, type) {
	switch (type) {

		case 'text':
			return <Typography variant="body1">{str}</Typography>

		case 'title':
			return <Typography variant="h6">{str}</Typography>

		case 'chords':
			return <Typography variant="button">{str}</Typography>

	}
}